import {reqBody} from "../index"

export default {
  login: (params, config) => {
    return reqBody({
      url: "/staff/login",
      method: "GET",
      params
    }, config)
  },
  getInfo: (params, config) => {
    return reqBody({
      url: "/staff/info",
      method: "GET",
      params
    }, config)
  },
  addStaff: (params, config) => {
    return reqBody({
      url: "/staff/addStaff",
      method: "POST",
      params
    }, config)
  },
  changeCustomer: (params, config) => {
    return reqBody({
      url: "/customer/change",
      method: "GET",
      params
    }, config)
  },
  customerList: (params, config) => {
    return reqBody({
      url: "/customer/page",
      method: "POST",
      params
    }, config)
  },
  saveCustomer: (params, config) => {
    return reqBody({
      url: "/customer/saveOrUpate",
      method: "POST",
      params
    }, config)
  },
  flowerCustomerList: (params, config) => {
    return reqBody({
      url: "/flowerCustomer/list",
      method: "POST",
      params
    }, config)
  },
  flowerCustomerPage: (params, config) => {
    return reqBody({
      url: "/flowerCustomer/page",
      method: "POST",
      params
    }, config)
  },
  flowerCustomerSave: (params, config) => {
    return reqBody({
      url: "/flowerCustomer/save",
      method: "POST",
      params
    }, config)
  },
  deptPage: (params, config) => {
    return reqBody({
      url: "/dept/page",
      method: "GET",
      params
    }, config)
  },
  deptSaveOrUpdate: (params, config) => {
    return reqBody({
      url: "/dept/saveOrUpdate",
      method: "POST",
      params
    }, config)
  },
  deptTree: (params, config) => {
    return reqBody({
      url: "/dept/tree",
      method: "GET",
      params
    }, config)
  },
}
